	module.exports = {"data":{"markdownRemark":{"html":"<h2 id=\"observe-what-is-running\"><a href=\"#observe-what-is-running\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Observe what is running</h2>\n<p>How do you know that an application running in your cluster is behaving correctly? The AuroraAPI helps with this in that it scrapes all applications in the cluster. The process\ngathers up data from the masterApi as well as from the <a href=\"aurora#managementInterface\">managementInterface</a>. This data is analysed in order to calculate an <a href=\"aurora#auroraStatus\">AuroraStatus</a> that is exposed to Prometheus and as a part of the AuroraAPI.</p>\n<p>It can then be used to create alerts in Prometheus along with rate of errors, log errors and 95% percentile responsetimes as an example.</p>","tableOfContents":"<ul>\n<li><a href=\"/frontpage/observe/#observe-what-is-running\">Observe what is running</a></li>\n</ul>","fields":{"slug":"/frontpage/observe/"},"frontmatter":{"date":null,"path":null,"title":""}}},"pathContext":{"slug":"/frontpage/observe/"}}

